<template>
    <div class="post-index" 
        v-loading.fullscreen.lock="loading" element-loading-text="拼命加载中"
        element-loading-spinner="el-icon-loading"
        element-loading-background="hsla(0,0%,100%,.7)" style="width: 100%">
        <div class="auth-success">
            <div class="header">
                <div class="header-item padding-right" :class="{headerItemActive:postHeadTab == 0}" @click="postHeadTapBtn(0)">
                    <span>正在招聘</span>
                    <span class="right-red-slot" v-if="recruitingNum > 0">{{recruitingNum}}</span>
                    <i :class="{isFirstActive:postHeadTab == 0}"></i>
                </div>
                <div class="header-item padding-right" :class="{headerItemActive:postHeadTab == 1}" @click="postHeadTapBtn(1)">
                    <span>待上架</span>
                    <span class="right-red-slot" v-if="waitingNum > 0">{{waitingNum}}</span>
                    <i :class="{isActive:postHeadTab == 1}"></i>
                </div>
                <div class="header-item" :class="{headerItemActive:postHeadTab == 2}" @click="postHeadTapBtn(2)">
                    <span>审核中</span>
                    <span class="right-red-slot" v-if="underReviewNum > 0">{{underReviewNum}}</span>
                    <i :class="{isActive:postHeadTab == 2}"></i>
                </div>
                <div class="header-item" :class="{headerItemActive:postHeadTab == 3}" @click="postHeadTapBtn(3)">
                    <span>已下架</span>
                    <span class="right-red-slot" v-if="hasRemovedNum > 0">{{hasRemovedNum}}</span>
                    <i :class="{isActive:postHeadTab == 3}"></i>
                </div>
            </div>
            <div class="post-list-all" v-if="certificationStatus == 3">
                <div class="list-no-data" v-if="recruitingListState == true && listTotalNum == 0 && postHeadTab == 0">
                    <img :src="recruitingListBack" class="bo-data-icon">
                    <p class="recruiting-tips">您还没有正在招聘的职位</p>
                    <p class="recruiting-tips recruiting-tips-two">快去发布吧</p>
                    <!-- <a class="recruiting-to-release" href="javascript:;" @click="releaseNewPosition">
                        去发布
                    </a> -->
                </div>
                <div class="list-no-data" v-else-if="recruitingListState == true && listTotalNum == 0 && postHeadTab == 1">
                    <img :src="recruitingListBack" class="bo-data-icon">
                    <p class="recruiting-tips">您当前还没有待上架的职位</p>
                    <p class="recruiting-tips recruiting-tips-two">快去发布吧</p>
                    <!-- <a class="recruiting-to-release" href="javascript:;" @click="releaseNewPosition">
                        去发布
                    </a> -->
                </div>
                <div class="list-no-data" v-else-if="recruitingListState == true && listTotalNum == 0 && postHeadTab == 2">
                    <img :src="recruitingListBack" class="bo-data-icon">
                    <p class="recruiting-tips">亲，您暂时还没有审核中的职位哦</p>
                    <!-- <a class="recruiting-to-release" href="javascript:;" @click="releaseNewPosition">
                        去发布
                    </a> -->
                </div>
                <div class="list-no-data" v-else-if="recruitingListState == true && listTotalNum == 0 && postHeadTab == 3">
                    <img :src="recruitingListBack" class="bo-data-icon">
                    <p class="recruiting-tips">亲，您还没有已下架的职位哦</p>
                </div>
                <div class="post-list-th" v-else v-for="(item,index) in postitionList" :key="index">
                    <div class="post-list-th-item">
                        <div class="item-head">
                            <div class="head-left">
                                <img :src="item.logoUrl" class="customer-logo"/>
                                <span class="customer-name">{{item.corpName}}</span>
                            </div>
                            <span class="customer-city" v-if="item.workProvinceCode || item.workCityCode || item.workDistCode">{{item.workProvinceCode}}·{{item.workCityCode}}</span>
                            <span class="customer-city" v-else>无</span>
                        </div>
                        <div class="item-content">
                            <div class="content-left" @click="stumpPostDetails(item.id,item.alias)">
                                <div class="one">
                                    <span class="post-type-company">
                                        {{item.alias}}-
                                        <span v-if="item.jobTypeCode3">{{item.jobTypeCode3}}</span>
                                        <span v-else-if="item.jobTypeCode2">{{item.jobTypeCode2}}</span>
                                    </span>
                                    <el-tag class="recruit-type" v-if="item.recruitDemandType == 1 || item.recruitDemandType == null || item.recruitDemandType == ''">社会工</el-tag>
                                    <el-tag class="recruit-type" v-if="item.recruitDemandType == 5">小时工</el-tag>
                                    <el-tag class="recruit-type" v-if="item.recruitDemandType == 8">学生工</el-tag>
                                </div>
                                <div class="two">
                                    <span class="salary-name">综合薪资</span>
                                    <span class="salary-val" v-if="item.salaryFrom && item.salaryTo">{{item.salaryFrom}}-{{item.salaryTo}}元/月</span>
                                </div>
                            </div>
                            <div class="content-right" @click="stumpQdsJobSeeker(item.corpId)">
                                <span class="post-job">已投递</span>
                                <span class="post-job-num" v-if="item.applyCount">{{item.applyCount}}</span>
                                <span class="post-job-num" v-else>0</span>
                            </div>
                        </div>
                        <div class="item-foot" v-if="postHeadTab == 0">
                            <a href="javascript:;" class="foot-btn look-btn" @click="preview(item.ylPositionId)">预览</a>
                            <a href="javascript:;" class="foot-btn refresh-btn" @click="postRefresh(item.id)">刷新</a>
                            <a href="javascript:;" class="foot-btn lower-self" @click="postLower(item.id)">下架</a>
                        </div>
                        <div class="item-foot" v-else-if="postHeadTab == 1">
                            <el-popover
                                v-if="item.unqualified"
                                placement="bottom"
                                width="300"
                                trigger="click"
                                :content="item.unqualified">
                                <el-button type="danger" class="fail-reason" slot="reference">失败原因</el-button>
                            </el-popover>
                            <a href="javascript:;" class="foot-btn look-btn" @click="postEdit(item.id)">编辑</a>
                            <a href="javascript:;" class="foot-btn refresh-btn" @click="postUpper(item.id)">上架</a>
                        </div>
                        <div class="item-foot" v-else-if="postHeadTab == 3">
                            <a href="javascript:;" class="foot-btn look-btn" @click="postEdit(item.id)">编辑</a>
                            <a href="javascript:;" class="foot-btn refresh-btn" @click="postUpper(item.id)">上架</a>
                        </div>
                    </div>
                </div>
                <infinite-loading @infinite="infiniteRecruiting" v-if="listTotalNum > 10">
                    <span slot="spinner" class="infinite-load">
                        <span>亲爱的，您慢点，我加载呢~</span>
                    </span>
                    <span slot="no-more" class="infinite-load">
                        数据已全部加载
                    </span>
                </infinite-loading>
            </div>
            <div v-else class="list-no-data list-no-auth">
                <img :src="recruitingListBack" class="bo-data-icon">
                <p class="recruiting-tips">企业认证成功后，即可解锁该模块哦~</p>
            </div>
        </div>
        <nav-tab :fatherActive='active'></nav-tab>
        <!-- popup的mask -->
        <div class="mask" v-show="refreshPopup"></div>
        <!--刷新model -->
        <div class="score_modal" v-if="closeRefreshShelfTipLstorage == 'false'" v-show="refreshPopup">
            <p class="f-28 c-12221F mb-2 lh-1 mb-10">刷新职位，将花费您10积分</p>
            <div class="refresh-check">
                <el-checkbox v-model="refreshShelfChecked" class="el-checkboxno">我知道了，下次不再提示</el-checkbox>
            </div>
            <div class="modal_footerBtn">
                <div class="btn" @click="hideRefreshPopup">取消</div>
                <div class="btn" @click="refreshSure">确定</div>
            </div>
        </div>
    </div>
</template>

<script>
import Util from '@/js/mUtils';
import navTab from "@/components/layout/tab.vue";
import {DLDict,DLArea,DLPType,DLIndustry} from '@/js/dl-code';
import recruitingListBack from "@/assets/post/recruiting_list_back.png";
import customerLogo from "@/assets/post/customer_logo_demo.png";
import { Notify  } from 'vant';
import InfiniteLoading from 'vue-infinite-loading';                     //分页加载组件
import { MessageBox  } from 'mint-ui';
export default {
    data() {
        return {
            globalApiUrl: domain.testUrl,       // 字典数据
            ossGlobalApiUrl: domain.ossDomain,  // oss全局域名
            qdsGlobalApiUrl: domain.qdsDomain,       // 快招渠道商全局域名
            postHeadTab: 0,
            clientHeight: {
                height: ""
            },
            recruitingListBack: recruitingListBack, //没有数据
            customerLogo: customerLogo,
            recruitingListState: false,         //是否显示无数据页面的开关
            listTotalNum: 1,
            postitionList: [],
            recruitingNum: 0,                  //正在招聘数量
            waitingNum: 0,                     //待上架数量
            underReviewNum: 0,                 //审核中数量
            hasRemovedNum: 0,                  //已下架
            loading: false,
            active: '3',
            listPageState: false,               //是否有分页
            certificationStatus: '',
            corpName: '',
            refreshPopup: false,            //刷新弹窗
            positionId: '',                     //点击刷新获取到的职位id
            refreshShelfChecked: '',            // 刷新不在提示复选框状态值
            closeRefreshShelfTipLstorage: '',   //在一个浏览器中不清除localStorage永远关闭刷新弹窗
            qdsUserId: '',              //渠道商id
        }
    },
    components: {
        Notify,
        navTab,
        MessageBox,
        InfiniteLoading,        //分页
    },
    created() {
        this.certificationStatus = sessionStorage.getItem('qdsMCertificationStatus')
        window.addEventListener('resize', this.getHeight);
        this.getHeight()
    },
    mounted() {
        window.scrollTo(0, 0);
        this.qdsUserId = localStorage.getItem('qdsSCorpUserId')
        this.closeRefreshShelfTipLstorage = localStorage.getItem('closeRefreshShelfTipLstorage' + this.qdsUserId)                         //刷新弹窗-是否勾选了不在提示状态
        if(this.closeRefreshShelfTipLstorage == null || this.closeRefreshShelfTipLstorage == undefined) {
            this.closeRefreshShelfTipLstorage = 'false'
        }
        if(this.$route.query.customerName) {
            this.corpName = this.$route.query.customerName;
            if(!this.corpName){
                this.itemPostCount();    
            }                    
            this.postHeadTapBtn(0)                                                             //在该页面，根据客户名称，自动筛选出与该客户关联的职位
        }else if(this.$route.query.postHeadTab) {
            this.postHeadTab = this.$route.query.postHeadTab
            this.postHeadTapBtn(this.postHeadTab)
        }else {
            this.itemPostCount();
            this.getRecruitingList()
        }
        
    },
    destroyed(){
        window.removeEventListener('resize', this.getHeight)
    },
    methods: {
        getHeight(){
            this.clientHeight.height = window.innerHeight - 0 + 'px';
        },
        postHeadTapBtn(index) {
            this.postHeadTab = index
            if(this.postHeadTab == 0) {
                this.recruitingListState = false;
                this.$router.push("/qdsPostList?postHeadTab=" + 0);
                window.scrollTo(0, 0);
                this.postitionList = [];
                if(!this.corpName){
                    this.itemPostCount();    
                }  
                this.getRecruitingList();
            }else if(this.postHeadTab == 1) {
                this.recruitingListState = false;
                this.$router.push("/qdsPostList?postHeadTab=" + 1);
                window.scrollTo(0, 0);
                this.postitionList = [];
                if(!this.corpName){
                    this.itemPostCount();    
                }
                this.getWaitSelfList();
            }else if(this.postHeadTab == 2) {
                this.recruitingListState = false;
                this.$router.push("/qdsPostList?postHeadTab=" + 2);
                window.scrollTo(0, 0);
                this.postitionList = [];
                if(!this.corpName){
                    this.itemPostCount();    
                }
                this.getInAuditList();
            }else if(this.postHeadTab == 3) {
                this.recruitingListState = false;
                this.$router.push("/qdsPostList?postHeadTab=" + 3);
                window.scrollTo(0, 0);
                this.postitionList = [];
                if(!this.corpName){
                    this.itemPostCount();    
                }
                this.getOffShelfList();
            }
        },
        itemPostCount() {                   //职位每个状态的数量
            var that = this;
            var token = Util.getCookie('qdsMToken');
            that.axios(that.qdsGlobalApiUrl + 'channelDemand/selectDemandStateCount?token=' + token,{
                method: 'post'
            })
            .then( response=> {
                if(response.data.code == 10000) {           
                    that.recruitingNum = response.data.data.recruitingCount
                    that.waitingNum = response.data.data.stayShelfCount
                    that.underReviewNum = response.data.data.auditCount
                    that.hasRemovedNum = response.data.data.shelfCount
                }else {
                    Notify({
                        message: '获取职位状态数量，请联系客服021-22876396' + '(错误码: ' + response.data.code + ')',
                        duration: 2000,
                        background: '#FF6262'
                    });
                }
            })
            .catch( error=> {
            });
        },
        getRecruitingList() {           //正在招聘列表
            var that= this;
            var token = Util.getCookie('qdsMToken');
            that.postitionList = [];
            that.listTotalNum = '';
            that.pageSize = 1;
            that.statusNo = 1;
            that.axios(that.qdsGlobalApiUrl + 'channelDemand/getPage?token=' + token,{
                method: 'post',
                params: {
                    page: that.pageSize,
                    limit: 10,
                    statusNo: that.statusNo,
                    searchParams: {"corpName":that.corpName,"title":"","startTime":"","endTime":"","workProvinceCode":"","workCityCode":"","workDistCode":""}
                }
            })
            .then(response=>{
                if(response.data.code == 10000) {
                    response.data.data.items.forEach(item => {
                        item.workProvinceCode = DLArea.getName(item.workProvinceCode)
                        item.workCityCode = DLArea.getName(item.workCityCode)
                        item.workDistCode = DLArea.getName(item.workDistCode)
                        if(item.jobTypeCode3) {
                            item.jobTypeCode3 = DLPType.getName(item.jobTypeCode3)
                        }else if(item.jobTypeCode2) {
                            item.jobTypeCode2 = DLPType.getName(item.jobTypeCode2)
                        }
                        // if(item.balanceType != null) {
                        //     item.balanceType = item.balanceType.split(",")
                        //     for(var i = 0;i < item.balanceType.length;i++){                          //结算标准数组去空   
                        //         if(item.balanceType[i] ==''){
                        //             item.balanceType.splice(i,1);
                        //             i = i-1;
                        //         }
                        //     }
                        // }
                    });
                    that.listTotalNum = response.data.data.totalNum;
                    if(that.listTotalNum == 0) {
                        that.recruitingListState = true;
                    }else if(that.listTotalNum > 10) {
                        that.listPageState = true
                    }else if(that.listTotalNum <= 10){
                        that.listPageState = false
                    }
                    that.postitionList = response.data.data.items;
                }else {
                    that.recruitingListState = true;
                    Notify({
                        message: '正在招聘列表数据加载异常，请联系客服021-22876396' + '(错误码: ' + response.data.code + ')',
                        duration: 2000,
                        background: '#FF6262'
                    });
                }
            })
            .catch( error=> {
            });
        },
        getWaitSelfList() {         //待上架列表
            var that= this;
            var token = Util.getCookie('qdsMToken');
            that.postitionList = [];
            that.listTotalNum = '';
            that.pageSize = 1;
            that.statusNo = 2;
            that.axios(that.qdsGlobalApiUrl + 'channelDemand/getPage?token=' + token,{
                method: 'post',
                params: {
                    page: that.pageSize,
                    limit: 10,
                    statusNo: that.statusNo,
                    searchParams: {"corpName":that.corpName,"title":"","startTime":"","endTime":"","workProvinceCode":"","workCityCode":"","workDistCode":""}
                }
            })
            .then(response=>{
                if(response.data.code == 10000) {
                    response.data.data.items.forEach(item => {
                        item.workProvinceCode = DLArea.getName(item.workProvinceCode)
                        item.workCityCode = DLArea.getName(item.workCityCode)
                        item.workDistCode = DLArea.getName(item.workDistCode)
                        if(item.jobTypeCode3) {
                            item.jobTypeCode3 = DLPType.getName(item.jobTypeCode3)
                        }else if(item.jobTypeCode2) {
                            item.jobTypeCode2 = DLPType.getName(item.jobTypeCode2)
                        }
                        // if(item.balanceType != null) {
                        //     item.balanceType = item.balanceType.split(",")
                        //     for(var i = 0;i < item.balanceType.length;i++){                          //结算标准数组去空   
                        //         if(item.balanceType[i] ==''){
                        //             item.balanceType.splice(i,1);
                        //             i = i-1;
                        //         }
                        //     }
                        // }
                    });
                    that.listTotalNum = response.data.data.totalNum;
                    if(that.listTotalNum == 0) {
                        that.recruitingListState = true;
                    }else if(that.listTotalNum > 10) {
                        that.listPageState = true
                    }else if(that.listTotalNum <= 10){
                        that.listPageState = false
                    }
                    that.postitionList = response.data.data.items;
                }else {
                    that.recruitingListState = true;
                    Notify({
                        message: '待上架列表数据加载异常，请联系客服021-22876396' + '(错误码: ' + response.data.code + ')',
                        duration: 2000,
                        background: '#FF6262'
                    });
                }
            })
            .catch( error=> {
            });
        },
        getInAuditList() {                  //审核中列表
            var that= this;
            var token = Util.getCookie('qdsMToken');
            that.postitionList = [];
            that.listTotalNum = '';
            that.pageSize = 1;
            that.statusNo = 3;
            that.axios(that.qdsGlobalApiUrl + 'channelDemand/getPage?token=' + token,{
                method: 'post',
                params: {
                    page: that.pageSize,
                    limit: 10,
                    statusNo: that.statusNo,
                    searchParams: {"corpName":that.corpName,"title":"","startTime":"","endTime":"","workProvinceCode":"","workCityCode":"","workDistCode":""}
                }
            })
            .then(response=>{
                if(response.data.code == 10000) {
                    response.data.data.items.forEach(item => {
                        item.workProvinceCode = DLArea.getName(item.workProvinceCode)
                        item.workCityCode = DLArea.getName(item.workCityCode)
                        item.workDistCode = DLArea.getName(item.workDistCode)
                        if(item.jobTypeCode3) {
                            item.jobTypeCode3 = DLPType.getName(item.jobTypeCode3)
                        }else if(item.jobTypeCode2) {
                            item.jobTypeCode2 = DLPType.getName(item.jobTypeCode2)
                        }
                        // if(item.balanceType != null) {
                        //     item.balanceType = item.balanceType.split(",")
                        //     for(var i = 0;i < item.balanceType.length;i++){                          //结算标准数组去空   
                        //         if(item.balanceType[i] ==''){
                        //             item.balanceType.splice(i,1);
                        //             i = i-1;
                        //         }
                        //     }
                        // }
                    });
                    that.listTotalNum = response.data.data.totalNum;
                    if(that.listTotalNum == 0) {
                        that.recruitingListState = true;
                    }else if(that.listTotalNum > 10) {
                        that.listPageState = true
                    }else if(that.listTotalNum <= 10){
                        that.listPageState = false
                    }
                    that.postitionList = response.data.data.items;
                }else {
                    that.recruitingListState = true;
                    Notify({
                        message: '审核中列表数据加载异常，请联系客服021-22876396' + '(错误码: ' + response.data.code + ')',
                        duration: 2000,
                        background: '#FF6262'
                    });
                }
            })
            .catch( error=> {
            });
        },
        getOffShelfList() {             //已下架列表
            var that= this;
            var token = Util.getCookie('qdsMToken');
            that.postitionList = [];
            that.listTotalNum = '';
            that.pageSize = 1;
            that.statusNo = 4;
            that.axios(that.qdsGlobalApiUrl + 'channelDemand/getPage?token=' + token,{
                method: 'post',
                params: {
                    page: that.pageSize,
                    limit: 10,
                    statusNo: that.statusNo,
                    searchParams: {"corpName":that.corpName,"title":"","startTime":"","endTime":"","workProvinceCode":"","workCityCode":"","workDistCode":""}
                }
            })
            .then(response=>{
                if(response.data.code == 10000) {
                    response.data.data.items.forEach(item => {
                        item.workProvinceCode = DLArea.getName(item.workProvinceCode)
                        item.workCityCode = DLArea.getName(item.workCityCode)
                        item.workDistCode = DLArea.getName(item.workDistCode)
                        if(item.jobTypeCode3) {
                            item.jobTypeCode3 = DLPType.getName(item.jobTypeCode3)
                        }else if(item.jobTypeCode2) {
                            item.jobTypeCode2 = DLPType.getName(item.jobTypeCode2)
                        }
                        // if(item.balanceType != null) {
                        //     item.balanceType = item.balanceType.split(",")
                        //     for(var i = 0;i < item.balanceType.length;i++){                          //结算标准数组去空   
                        //         if(item.balanceType[i] ==''){
                        //             item.balanceType.splice(i,1);
                        //             i = i-1;
                        //         }
                        //     }
                        // }
                    });
                    that.listTotalNum = response.data.data.totalNum;
                    if(that.listTotalNum == 0) {
                        that.recruitingListState = true;
                    }else if(that.listTotalNum > 10) {
                        that.listPageState = true
                    }else if(that.listTotalNum <= 10){
                        that.listPageState = false
                    }
                    that.postitionList = response.data.data.items;
                }else {
                    that.recruitingListState = true;
                    Notify({
                        message: '已下架列表数据加载异常，请联系客服021-22876396' + '(错误码: ' + response.data.code + ')',
                        duration: 2000,
                        background: '#FF6262'
                    });
                }
            })
            .catch( error=> {
            });
        },
        infiniteRecruiting($state) {                                            //职位列表分页
            let token = Util.getCookie('qdsMToken');
            let that= this;
            if(that.postHeadTab == 0) {         //切换头部状态来切换传给后端的statusNo值
                that.statusNo = 1;
            }else if(that.postHeadTab == 1) {
                that.statusNo = 2;
            }else if(that.postHeadTab == 2) {
                that.statusNo = 3;
            }else if(that.postHeadTab == 3) {
                that.statusNo = 4;
            }
            if(that.listPageState) {
                that.pageSize = that.pageSize + 1;
                that.axios(that.qdsGlobalApiUrl + 'channelDemand/getPage?token=' + token,{
                    method: 'post',
                    params: {
                        page: that.pageSize,
                        limit: 10,
                        statusNo: that.statusNo,
                        searchParams: {"corpName":that.corpName,"title":"","startTime":"","endTime":"","workProvinceCode":"","workCityCode":"","workDistCode":""}
                    }
                })
                .then(response=>{
                    if(response.data.code == 10000) {
                        if (response.data.data.items) {
                            if(response.data.data.items.length < 10) {
                                that.listPageState = false
                            }else {
                                that.listPageState = true
                            }
                            response.data.data.items.forEach(item => {
                                item.workProvinceCode = DLArea.getName(item.workProvinceCode)
                                item.workCityCode = DLArea.getName(item.workCityCode)
                                item.workDistCode = DLArea.getName(item.workDistCode)
                                if(item.jobTypeCode3) {
                                    item.jobTypeCode3 = DLPType.getName(item.jobTypeCode3)
                                }else if(item.jobTypeCode2) {
                                    item.jobTypeCode2 = DLPType.getName(item.jobTypeCode2)
                                }
                            });
                            that.postitionList = that.postitionList.concat(response.data.data.items);
                            $state.loaded();
                        }else {
                            $state.complete();
                        }
                    }else {
                        Notify({
                            message: '列表分页数据加载异常，请联系客服021-22876396' + '(错误码: ' + response.data.code + ')',
                            duration: 2000,
                            background: '#FF6262'
                        });
                    }
                })
                .catch( error=> {
                });
            }else {
                $state.complete();
            }
        },
        preview(ylPositionId) {
            var location_href = location.href;
            if (location_href.indexOf('m.qh.uat.youlanw.com') != -1) { 
                window.open('https://m.youlanw.com/zhaopin/jobDetail.html?jobId=' + ylPositionId)       								// c端m站正式环境
            } else { 				
                window.open('http:///m.yl.test.tkinghr.com/zhaopin/jobDetail.html?jobId=' + ylPositionId)        	// c端m站测试环境
            }
        },
        hideRefreshPopup() {                    //隐藏刷新弹窗
            this.refreshPopup = false
        },
        refreshSure() {                  //职位刷新-显示弹窗（没有点击不在提示的刷新按钮）
            var that = this;
            var token = Util.getCookie('qdsMToken');
            that.loading = true;
            that.axios(that.qdsGlobalApiUrl + 'channelDemand/refreshById?token=' + token,{
                method: 'post',
                params: {
                    id: that.positionId,
                },
                timeout: 10000
            })
            .then(response=>{
                if(response.data.code == 10000) {
                    that.loading = false;
                    that.positionId = '';
                    that.refreshPopup = false;
                    if(that.refreshShelfChecked == true) {  
                        localStorage.setItem('closeRefreshShelfTipLstorage'+ that.qdsUserId,'true')
                        that.closeRefreshShelfTipLstorage = localStorage.getItem('closeRefreshShelfTipLstorage' + that.qdsUserId)                 //刷新弹窗-不在提示
                        that.refreshPopup = false
                    }else{
                        that.refreshPopup = false
                    }
                    Notify({
                        message: '职位刷新成功',
                        duration: 2000,
                        background: '#20D3B3'
                    });
                    if(that.postHeadTab == 0) {
                        that.getRecruitingList()            //获取正在招聘列表
                    }else if(that.postHeadTab == 2) {
                        that.getInAuditList()               //获取审核中列表
                    }
                }else {
                    that.loading = false;
                    that.positionId = '';
                    that.refreshPopup = false;
                    if(that.refreshShelfChecked == true) {  
                        localStorage.setItem('closeRefreshShelfTipLstorage'+that.qdsUserId,'true')
                        that.closeRefreshShelfTipLstorage = localStorage.getItem('closeRefreshShelfTipLstorage' + that.qdsUserId)                 //刷新弹窗-不在提示
                        that.refreshPopup = false
                    }else{
                        that.refreshPopup = false
                    }
                    Notify({
                        message: response.data.message + '(错误码: ' + response.data.code + ')',
                        duration: 2000,
                        background: '#FF6262'
                    });
                }
            })
            .catch( error=> {
                that.loading = false;
                that.positionId = '';
                that.refreshPopup = false;
                if(that.refreshShelfChecked == true) {  
                    localStorage.setItem('closeRefreshShelfTipLstorage'+that.qdsUserId,'true')
                    that.closeRefreshShelfTipLstorage = localStorage.getItem('closeRefreshShelfTipLstorage' + that.qdsUserId)                 //刷新弹窗-不在提示
                    that.refreshPopup = false
                }else{
                    that.refreshPopup = false
                }
                Notify({
                    message: '职位刷新失败，请联系客服021-22876396',
                    duration: 2000,
                    background: '#FF6262'
                });
            });
        },
        postRefresh(positionId) {                         //职位刷新弹窗确认-根据closeRefreshShelfTipLstorage状态是否显示隐藏刷新弹窗
            var that = this;
            var token = Util.getCookie('qdsMToken');
            if(that.closeRefreshShelfTipLstorage == 'false') {
                that.positionId = positionId;
                that.refreshPopup = true;
            }else if(that.closeRefreshShelfTipLstorage == 'true') {
                that.loading = true;
                that.axios(that.qdsGlobalApiUrl + 'channelDemand/refreshById?token=' + token,{
                    method: 'post',
                    params: {
                        id: positionId,
                    },
                    timeout: 10000
                })
                .then(response=>{
                    if(response.data.code == 10000) {
                        that.positionId = '';
                        that.loading = false;
                        Notify({
                            message: '职位刷新成功',
                            duration: 2000,
                            background: '#20D3B3'
                        });
                    }else {
                        that.positionId = '';
                        that.loading = false;
                        Notify({
                            message: response.data.message + '(错误码: ' + response.data.code + ')',
                            duration: 2000,
                            background: '#FF6262'
                        });
                    }
                })
                .catch( error=> {
                    that.positionId = '';
                    that.loading = false;
                    Notify({
                        message: '职位刷新失败，请联系客服021-22876396',
                        duration: 2000,
                        background: '#FF6262'
                    });
                });
            }
        },
        postLower(positionId) {                   //职位下架
            var that= this;
            var token = Util.getCookie('qdsMToken');
            that.loading = true;
            that.axios(that.qdsGlobalApiUrl + 'channelDemand/updateStatusNoById?token=' + token,{
                method: 'post',
                params: {
                    demandId: positionId,
                    operationType: 2,
                }
            })
            .then(response=>{
                if(response.data.code == 10000) {
                    that.loading = false;
                    Notify({
                        message: '职位下架成功',
                        duration: 2000,
                        background: '#20D3B3'
                    });
                    that.postHeadTab = 3
                    if(!that.corpName){
                        that.itemPostCount();    
                    }
                    that.getOffShelfList()
                }else {
                    that.loading = false;
                    Notify({
                        message: '职位下架失败，请联系客服021-22876396' + '(错误码: ' + response.data.code + ')',
                        duration: 2000,
                        background: '#FF6262'
                    });
                }
            })
            .catch( error=> {
                that.loading = false;
                Notify({
                    message: '职位下架失败，请联系客服021-22876396',
                    duration: 2000,
                    background: '#FF6262'
                });
            });
        },
        postEdit(id) {
            this.$router.push('/qdsReleaseNewPost?positionId=' + id)
        },
        postUpper(positionId) {               //职位上架
            var that= this;
            var token = Util.getCookie('qdsMToken');
            that.loading = true;
            that.axios(that.qdsGlobalApiUrl + 'channelDemand/updateStatusNoById?token=' + token,{
                method: 'post',
                params: {
                    demandId: positionId,
                    operationType: 1,
                }
            })
            .then(response=>{
                if(response.data.code == 10000) {
                    that.loading = false;
                    Notify({
                        message: '职位上架成功',
                        duration: 2000,
                        background: '#20D3B3'
                    });
                    if(that.postHeadTab == 1) {
                        that.postHeadTab = 2
                        if(!that.corpName){
                            that.itemPostCount();    
                        }
                        that.getInAuditList();          //重新获取审核中列表
                    }else if(that.postHeadTab == 3) {
                        that.postHeadTab = 2
                        if(!that.corpName){
                            that.itemPostCount();    
                        }
                        that.getInAuditList();           //重新获取审核中列表
                    }
                }else {
                    that.loading = false;
                    Notify({
                        message: '职位上架失败，请联系客服021-22876396' + '(错误码: ' + response.data.code + ')',
                        duration: 2000,
                        background: '#FF6262'
                    });
                }
            })
            .catch( error=> {
                that.loading = false;
                Notify({
                    message: '职位上架失败，请联系客服021-22876396',
                    duration: 2000,
                    background: '#FF6262'
                });
            });
        },
        stumpPostDetails(positionId,companyAlias) {          //跳转到职位详情
            this.$router.push('/postDetails?positionId=' + positionId + "&" + "companyAlias=" + companyAlias + "&" + "postHeadTab=" + this.postHeadTab)
        },
        stumpQdsJobSeeker(corpId) {       //职位-已投递记录-点击后，跳转至“求职者”页面,示该页面下“新投递”分栏下，自动筛选出与客户关联职位相关的求职者记录。
            this.$router.push("/qdsSearchResume?corpId=" + corpId + '&selected=' + '2')   
        }
    }
}
</script>

<style scoped>
a{
    text-decoration: none;
}
.post-index{
    background-color: #f5f5f5;
}
.post-index .header{
    display: -webkit-flex;
    display: flex;
    justify-content: space-around;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 2.4375rem;
    line-height: 2.4375rem;
    background-color: #fff;
}
.header .header-item{
    position: relative;
    /* width: 33%; */
    height: 100%;
    font-size: 1rem;
    color: #0B231F;
    text-align: center;
    cursor: pointer;
    /* font-weight: bold; */
}
.header .right-red-slot{
    position: absolute;
    top: .6875rem;
    right: .0625rem;
    display: flex;
    display: -webkit-flex;
    width: .75rem;
    height: .75rem;
    justify-content: center;
    align-items: center;
    -webkit-transform: translateY(-50%) translateX(100%);
    transform: translateY(-50%) translateX(100%);
    background-color: #FB5C55;
    border-radius: 50%;
    color: #fff;
    font-size: .5rem;
    text-align: center;
    border: 1px solid #fff;
}
.header .headerItemActive{
    color: #0B231F;
}
.header .padding-right{
    /* padding-right: 1.3125rem; */
}
.header-item .isActive{
    position: absolute;
    left: 0.5rem;
    bottom: 0;
    align-items: flex-end;
    display: inline-block;
    width: 2.0625rem;
    height: .1875rem;
    background-color: #20D3B3;
}
.header-item .isFirstActive{
    position: absolute;
    left: 1rem;
    bottom: 0;
    align-items: flex-end;
    display: inline-block;
    width: 2.0625rem;
    height: .1875rem;
    background-color: #20D3B3;
}
.list-no-data{
    padding-top: 9.5rem;
    margin: auto 0;
    text-align: center;
}
.list-no-auth{
    padding-top: 12rem;
}
.list-no-data .bo-data-icon{
    display: inline-block;
    width: 12.15625rem;
    height: 6.96875rem;
}
.list-no-data .recruiting-tips{
    padding-top: 1.9375rem;
    font-size: 14px;
    color: #354242;
}
.list-no-data .recruiting-tips-two{
    padding-top: 0;
}
.post-list-all{
    padding-top:  2.4375rem;
    padding-bottom: 3.0625rem;
}
.post-list-th-item{
    margin: .625rem .9375rem 0 .9375rem;
    background-color: #fff;
}
.post-list-th-item .item-head{
    display: -webkit-flex;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 .625rem;
    padding: .625rem 0;
    border-bottom: 1px solid #E5E5E5;
}
.head-left{
    display: flex;
    align-items: center;
}
.head-left .customer-logo{
    display: inline-block;
    width: 1.6875rem;
    height: 1.6875rem;
    border-radius: 50%;
}
.head-left .customer-name{
    display: inline-block;
    padding-left: .5625rem;
    font-size: .8125rem;
    color: #384242;
}
.customer-city{
    font-size: .6875rem;
    color: #818D8D;
}
.post-list-th-item .item-content {
    display: -webkit-flex;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: .625rem .9375rem .625rem .625rem;
    border-bottom: 1px solid #E5E5E5;
}
.item-content .content-left{
    width: 80%;
}
.content-left .one{
    display: -webkit-flex;
    display: flex;
    align-items: center;
    padding-bottom: .7375rem;
}
.content-left .post-type-company{
    font-size: 1.0625rem;
    color: #384242;
    font-weight: bold;
}
.content-left .recruit-type{
    display: flex;
    display: -webkit-flex;
    justify-content: center;
    align-items: center;
    margin-left: .6875rem;
    width: 3.375rem;
    height: 1.1rem;
    line-height: 1.1rem;
    font-size: .6875rem;
    color: #417EF8;
    border-radius: 1px;
}
.item-content .two{
    display: -webkit-flex;
    display: flex;
    align-items: center;
}
.item-content .salary-name{
    font-size: .6875rem;
    color: #818D8D;
}
.item-content .salary-val{
    display: inline-block;
    padding-left: .625rem;
    font-size: 1.0625rem;
    color: #FB5C55;
    /* font-weight: bold; */
}
.item-content .content-right{
    /* padding-top: .75rem; */
}
.content-right .post-job{
    font-size: .75rem;
    color: #384242;
}
.content-right .post-job-num{
    font-size: 1.875rem;
    color: #384242;
}
.item-foot{
    display: -webkit-flex;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: .625rem .9375rem .625rem 0;
}
.foot-btn{
    display: flex;
    display: -webkit-flex;
    justify-content: center;
    align-items: center;
    margin-left: .625rem;
    width: 3rem;
    height: 1.5rem;
    /* line-height: 1.5rem;
    text-align: center; */
    border-radius: .25rem;
}
.fail-reason{
    display: inline-block;
    margin-left: .625rem;
    padding: 0;
    width: 3.875rem;
    height: 1.5rem;
    line-height: 1.5rem;
    text-align: center;
    border-radius: .25rem;
    font-size: .8125rem;
    color: #fff;
    background-color: #FB5C55;
}
.look-btn{
    background-color: #E5E5E5;
    font-size: .8125rem;
    color: #354242;
}
.refresh-btn{
    background-color: #FF963D;
    font-size: .8125rem;
    color: #FFFFFF;
}
.lower-self{
    background-color: #5FBCA8;
    font-size: .8125rem;
    color: #FFFFFF;
}
.infinite-load{
    display: inline-block;
    padding: .625rem 0;
    font-size: .875rem;
    color: #999;

}
/* 刷新弹窗 */
.mask {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0);
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 99;
  opacity:0.6;
}
.score_modal {
  width: 18.4375rem;
  height: 11.875rem;
  border-radius: 0.25rem;
  background: #fff;
  position: fixed;
  margin: auto;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 999;
  padding: 1.84375rem 1.03125rem 1.78125rem;
  box-sizing: border-box;
  text-align: center;  
}

.score_modal .modal_footerBtn {
  display: flex;
  display: -webkit-flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 1.875rem;
}

.score_modal .modal_footerBtn > div {
  width: 7.5rem;
  height: 2.0625rem;
  display: flex;
  display: -webkit-flex;
  justify-content: center;
  align-items: center;
  border-radius: 1.03125rem;
  text-align: center;
  cursor: pointer;
}

.score_modal .modal_footerBtn > div:first-child {
  background: #fff;
  border: 1px solid #818d8d;
}

.score_modal .modal_footerBtn > div:last-child {
  background: #ff963d;
  color: #fff;
}
.score_modal .refresh-check{
    padding-top: 1.875rem;
}
</style>





